* {
  margin: 0;
  padding: 0;
}

.block {
  background-color: cyan;
  color: green;
}

.block .block-item {
  font-size: 50px;
}

.block .block-item #item {
  color: crimson;
  font-size: 20px;
}

.block .block-item #item .selec {
  font-size: 48px;
}

.selec {
  font-size: 20px !important;
}

.selec {
  font-size: 35px !important;
}

.block {
  color: red;
  background-color: green;
}

.block:before {
  content: 'Im PSEUDO before!';
}

.block.mod {
  background-color: blue;
}

.filter {
  padding: 24px;
}

.filter__input {
  border: 2px solid red;
  display: block;
  margin-bottom: 20px;
}

.filter__btn {
  background-color: cyan;
  padding: 5px 10px;
  border-radius: 20px;
}

.filter__btn:hover {
  background-color: #006666;
}

.filter__btn_cancel {
  background-color: red;
  color: #fff;
}

.filter__btn_cancel:hover {
  background-color: #ff6666;
}

.container {
  margin: 20px 0;
  height: 800px;
  background-color: #999;
  position: relative;
}

.container .colored-block {
  width: 273px;
  height: 155px;
  background-color: cyan;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -77.5px;
  margin-left: -136.5px;
}

.container .colored-block2 {
  background-color: red;
  width: 500px;
  height: 220px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -110px;
  margin-left: -250px;
  font-size: 60px;
}

